:root {
  --radius: 5.5;
  --size: 4;
}
h1{
  text-align: center;
}
.mainInfo {
  min-height: 40vh;
  background: hsl(var(--hue), 20%, 70%);
  overflow: hidden;
  margin: 0;
  padding: 0;
  background: #fcedd8;

}
[type='checkbox'] {
  position: absolute;
  left: 100%;
  opacity: 0;
  height: 0;
  width: 0;
}
#info{
  width: 90%;
}
table{
  text-align: center;
}
th{
  width: 25%;
}
.scene {
    perspective: 800px;
    transform-style: preserve-3d;
    height: 5vh;
    width: 100vw;
    display: flex;
    justify-content: left;
    margin-top: 4%;
    margin-left: 4% ;
    background: #fcedd8;
  }
  .plane {
    height: 5vmin;
    width: 5vmin;
    transform-style: preserve-3d;
    transform: rotateX(-40deg) rotateY(-45deg) rotateX(90deg);
  }
  .cube,
  .shadow {
  
    --width: var(--size);
    --height: var(--size);
    --depth: var(--size);
    height: calc(var(--depth) * 1vmin);
    width: calc(var(--width) * 1vmin);
    transform-style: preserve-3d;
    position: absolute;
    font-size: 1rem;
    animation: jump 0.8s calc(var(--d) * 1s) infinite;
    transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
  }
  .cube > div:nth-of-type(1),
  .shadow > div:nth-of-type(1) {
    background: hsl(var(--hue), 60%, 50%);
    height: calc(var(--height) * 1vmin);
    width: 100%;
    transform-origin: 50% 50%;
    transform: rotateX(-90deg);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateX(-90deg) translate3d(0, 0, calc((var(--depth) / 2) * 1vmin));
  }
  .cube > div:nth-of-type(2),
  .shadow > div:nth-of-type(2) {
    background: hsl(var(--hue), 60%, 60%);
    height: calc(var(--height) * 1vmin);
    width: 100%;
    transform-origin: 50% 50%;
    transform: translate(-50%, -50%) rotateX(-90deg) rotateY(180deg) translate3d(0, 0, calc((var(--depth) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(3),
  .shadow > div:nth-of-type(3) {
    background: hsl(var(--hue), 60%, 60%);
    height: calc(var(--height) * 1vmin);
    width: calc(var(--depth) * 1vmin);
    transform: translate(-50%, -50%) rotateX(-90deg) rotateY(90deg) translate3d(0, 0, calc((var(--width) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(4),
  .shadow > div:nth-of-type(4) {
    background: hsl(var(--hue), 60%, 55%);
    height: calc(var(--height) * 1vmin);
    width: calc(var(--depth) * 1vmin);
    transform: translate(-50%, -50%) rotateX(-90deg) rotateY(-90deg) translate3d(0, 0, calc((var(--width) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(5),
  .shadow > div:nth-of-type(5) {
    background: hsl(var(--hue), 60%, 45%);
    height: calc(var(--depth) * 1vmin);
    width: calc(var(--width) * 1vmin);
    transform: translate(-50%, -50%) translate3d(0, 0, calc((var(--height) / 2) * 1vmin));
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .cube > div:nth-of-type(6),
  .shadow > div:nth-of-type(6) {
    background: hsl(var(--hue), 60%, 65%);
    height: calc(var(--depth) * 1vmin);
    width: calc(var(--width) * 1vmin);
    transform: translate(-50%, -50%) translate3d(0, 0, calc((var(--height) / 2) * -1vmin)) rotateX(180deg);
    position: absolute;
    top: 50%;
    left: 50%;
  }
  .shadow {
    animation: none;
    background: radial-gradient(#000, transparent);
    transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    animation: shadow 0.8s calc(var(--d) * 1s) infinite;
  }
  .cube--00,
  .shadow--00 {
    background:  rgba(140, 211, 254, 0.5);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--01,
  .shadow--01 {
    background: #000;;
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
 
  .cube--02,
  .shadow--02 {
    background:#fcedd8;
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--03,
  .shadow--03 {
    background-color: rgba(255,165,0, 0.9);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--04,
  .shadow--04 {
    background-color: rgba(144,238,144, 0.9);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--05,
  .shadow--05 {
    background-color: rgba(0,0,255, 0.9);
    --x: 1;
    --y: 0;
    --d: -0.7;
  }
  .cube--6,
  .shadow--6 {
    --x: 0;
    --y: 1;
    --d: -0.1;
  }
  .cube--7,
  .shadow--7 {
    --x: 1;
    --y: 1;
    --d: 0;
  }
  @-moz-keyframes jump {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 3.5vmin);
    }
  }
  @-webkit-keyframes jump {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 3.5vmin);
    }
  }
  @-o-keyframes jump {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 3.5vmin);
    }
  }
  @keyframes jump {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 0);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), 3.5vmin);
    }
  }
  @-moz-keyframes shadow {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(0.85);
      opacity: 0.2;
    }
  }
  @-webkit-keyframes shadow {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(0.85);
      opacity: 0.2;
    }
  }
  @-o-keyframes shadow {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(0.85);
      opacity: 0.2;
    }
  }
  @keyframes shadow {
    0%, 30%, 100% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(1);
    }
    15% {
      transform: translate3d(calc((var(--x) * var(--radius)) * 1vmin), calc((var(--y) * var(--radius)) * -1vmin), calc(var(--size) * -0.5vmin)) scale(0.85);
      opacity: 0.2;
    }
  }